<template>
  <div class="container">
    <div class="image">
      <img
        src="data:image/png;base64,">
    </div>
    <div class="search-box">
      <span></span>
      <input type="text" ref="keywords" placeholder="请输入搜索关键词" @click="searchClasses()">
    </div>
    <!--class="history"-->
    <div class="m-search-page z-open" v-show="showPanel">
      <section class="m-search-page__b">
        <header class="m-search-page__h"><i class="icon-font i-hot"></i>热门搜索</header>
        <ul class="m-search-page__hotlist">
          <li class="m-search-page__hotitem f-border-1px" cmd="searchHot">公务员</li>
          <li class="m-search-page__hotitem f-border-1px" cmd="searchHot">考研政治</li>
          <li class="m-search-page__hotitem f-border-1px" cmd="searchHot">产品</li>
          <li class="m-search-page__hotitem f-border-1px" cmd="searchHot">前端开发</li>
          <li class="m-search-page__hotitem f-border-1px" cmd="searchHot">会计</li>
          <li class="m-search-page__hotitem f-border-1px" cmd="searchHot">英语</li>
          <li class="m-search-page__hotitem f-border-1px" cmd="searchHot">excel</li>
          <li class="m-search-page__hotitem f-border-1px" cmd="searchHot">高考数学</li>
          <li class="clearfix"></li>
        </ul>
      </section>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        showPanel: false
      }
    },
    methods: {
      searchClasses() {
        this.$refs.keywords.focus();
        this.$refs.keywords.placeholder = '';
        this.showPanel = true;
        this.$emit('showPanel', true);
      },
    }
  }
</script>
<style scoped>
  .container {
    position: fixed;
    z-index: 100;
    background-color: #fff;
    width: 100%;
    display: flex;
  }

  .container .image {
    width: .8rem;
    height: .8rem;
    margin: .25rem .3055556rem .25rem .3611111rem;
  }

  .container .image img {
    width: 100%;
    height: 100%;
  }

  .search-box {
    display: inline-block;
    position: relative;
    width: 8.5rem;
  }

  /*.search-box span {*/
    /*position: absolute;*/
    /*right: .5rem;*/
    /*top: .5rem;*/
    /*background-image: url(data:image/png;base64,*/
    /*);*/
    /*background-repeat: no-repeat;*/
    /*background-size: cover;*/
  /*}*/

  .search-box input {
    float: left;
    width: 96%;
    height: 0.666667rem;
    border: none;
    border-radius: .1rem;
    font-size: .30555556rem;
    line-height: 0.666667rem;
    font-weight: 800;
    margin-top: .3rem;
    background-color: #f2f2f2;
    text-align: center;
  }

  .download {
    flex: 0 0 80px;
    text-align: center;
    margin: .3rem .1rem;
    box-sizing: content-box;
    border-radius: 4px;
  }

  .history {
    position: relative;
    top: 40px;
    left: 55px;
    width: 82%;
    z-index: 12;
  }

  .m-search-page {
    display: block;
    position: relative;
    top: 45px;
    left: -20px;
    width: 100%;
  }

  .m-search-page__h {
    height: 40px;
    font-size: 14px;
    line-height: 40px;
    color: #909090;
  }

  .m-search-page__h .icon-font {
    line-height: 40px;
    margin-right: 5px;
    vertical-align: baseline;
  }

  .icon-font {
    position: relative;
    font-family: mfont;
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    font-variant: normal;
    line-height: 1;
    vertical-align: -2px;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    speak: none;
  }

  .i-hot:before {
    content: "\f01d";
  }

  .m-search-page__hotlist {
    padding: 0 0 2px;
    overflow: hidden;
  }

  .m-search-page__hotitem {
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid #d2deec;
    border-radius: 11px;
    padding: 0 16px;
    height: 23px;
    font-size: 14px;
    line-height: 23px;
    color: #404040;
  }

  @media only screen and (-webkit-min-device-pixel-ratio: 2)
    .f-border-1px:after {
      right: -100%;
      bottom: -100%;
      -webkit-transform: scale(0.5);
    }

    @media only screen and (-webkit-min-device-pixel-ratio: 1.5)
      .f-border-1px:after {
        right: -42.857%;
        bottom: -42.857%;
        -webkit-transform: scale(0.7);
      }

      .f-border-1px:after {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        -webkit-transform: scale(1);
        -webkit-transform-origin: 0 0;
        content: '';
        pointer-events: none;
      }
</style>



